#yearly-box{
    margin-inline: 15%;
    margin-top: 2%;
}
#yearly-data-box{
    display: flex;
    justify-content: space-between;
}
#Yearly-records{
    text-align: center;
    color: white;
    background-color: rgb(34, 92, 92);
}
#yearly-income {
    color: rgb(190, 190, 190);
    font-size: larger;
    padding: 20px;
}
.amount{
    text-align: right;
}

#income-title {
    text-align: left;
    margin-bottom: 10px;
}

.income-container {
    width: 80%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    background-color: rgb(24, 24, 24);
    padding-bottom: 5px;
    border-bottom: 0.5px rgba(84, 84, 84, 0.472) solid;
    color: white;
}
.total{
    border: none;
    border-top: 2px white solid;
}
.income-container-heading {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    padding: 10px;
    margin-bottom: 20px;
}
.header {
    display: flex;
    flex-direction: row;
}

.income-row {
    display: flex;
    flex-direction: row;
    border-top: 1px solid #ccc;
}


#yearly-income {
    width: 50%;
}
.yearly-record-line{
    min-width: 40%;
}
.record-list{
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
}
#yearly-container{
    display: flex;
}
@media screen and (width<1000px) {
    #yearly-box{
        margin: 5%;
    }
    #yearly-data-box{
        flex-direction: column;
        gap: 25px;
    }
    #yearly-income {
        width: 90%;
    }
}