.record-form {
    margin-inline: 2%;
    margin-top: 2%;
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
}
.form-field{
    background-color: transparent;
    border: none;
    border-bottom: 3px solid darkcyan;
    width: 25%;
    padding: 1%;
    margin: 1%;
    font-size: 20px;
    outline: none;
    color: white;
}
.option{
    color: black;
    background-color: transparent;
}
.record-btn{
    background-color: transparent;
    border: none;
    color: white;
    cursor: pointer;
    height: auto;
    font-size: 18px;
}
.remark{
    text-align: center;
    font-style: italic;
    color: #ff3907;
    display: flex;
    justify-content: center;
}
.sum{
    border-top: 2px rgba(64, 224, 208) solid;
    margin-top: 10px;
    padding-top: 10px;
}
.no-record{
    text-align: center;
}
#user-list-box{
    position: relative;
    margin: 1%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    width: max-content;
}
#user-list-box input{
    margin: 0;
    width: 100%;
    /* height: 100%; */
    padding: 3%;
}
#user-list:active{
    display: block;
}
#user-list{
    display: none;
    position: absolute;
    top: 0;
    margin-top: 22%;
    background-color: #191919;
    width: 100%;
    border-radius: 10px;
    z-index: 200;
    overflow: hidden;
}
.user-list-btn{
    display: flex;
    background-color: transparent;
    color: white;
    justify-content: space-between;
    padding: 3%;
    border: none;
    font-size: 18px;
}
.user-list-btn i{
    color: #c2c2c2;
    font-size: 15px;
}
.user-list-btn:hover{
    background-color: #252525;
}
@media screen and (width<1000px) {
    .record-form{
        flex-direction: column;
        align-items: center;
    }
    .form-field,#user-list-box{
        width: 80%;
        margin-top: 25px;
    }
    .record-btn{
        margin-top: 20px;
    }
    #user-list-box input{
        width: 98%;
        padding: 1%;
        padding-bottom: 3%;
    }
}