#signup-outer-box{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
#signup-box{
    width: 50%;
    padding: 2%;
    background-color: #101010aa;
    box-shadow: 1px 1px 30px 0px #05c2ac98;
    border-radius: 10px;
}
#signup-title{
    font-size: 35px;
    color: #00fbff;
    border-bottom: 4px #00fbff solid;
    margin-bottom: 40px;
    padding: 1%;
}
.signup-form-fields{
    background-color: transparent;
    outline: none;
    border: none;
    width: 40%;
    font-size: 20px;
    color: white;
    padding: 1%;
    border-bottom: 3px solid white;
    margin-bottom: 20px;
    margin-left: 40px;
}
#signup-warn{
    margin-left: 40px;
    font-size: 18px;
    font-style: italic;
}
#send-otp-btn{
    margin-top: 20px;
    display: block;
    margin-left: 40px;
    font-size: 22px;
    background-color: #027779;
    border: none;
    color: white;
    padding: 1%;
    border-radius: 10px;
    cursor: pointer;
}
#send-otp-btn:hover{
    background-color: #014648;
}
#send-otp-btn:active{
    transform: translateY(2px);
}
#login-btn{
    font-size: 15px;
    color: white;
    background-color: transparent;
    border: none;
    margin-left: 40px;
    margin-top: 20px;
    cursor: pointer;
}
#login-btn:hover{
    text-decoration: underline;
}
@media screen and (width<1000px) {
    #signup-box{
        width: 80%;
    }
    .signup-form-fields{
        width: 80%;
    }
}