#err-outer-box{
    margin-top: 60px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
#err-inner-box{
    width: 30%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-self: center;
}
#err-text{
    width: 100%;
    text-align: center;
    font-size: 22px;
    font-style: italic;
    color: white;
}
#err-img{
    text-align: center;
}
#err-img img{
    width: 100%;
}
@media screen and (width<1000px) {
    #err-inner-box{
        width: 80%;
    }
}