#outer-login-box {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
#login-box{
    width: 50%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    box-shadow: 1px 1px 50px 0px rgba(107, 240, 245, 0.394);
    padding: 1%;
    border-radius: 10px;
}
.login-box-part{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    padding: 1%;
    gap: 20px;
}
#login-2{
    gap: 20px;
}
#desc{
    font-size: 20px;
}
#img{
    width: 90%;
}
img{
    transition: all ease 2s;
    width: 100%;
}
#login-title{
    font-size: 40px;
    margin-bottom: 30px;
    padding-bottom: 10px;
    border-bottom: 3px double #06869c;
}
.form-fields{
    background-color: transparent;
    outline: none;
    border: none;
    border-bottom: 3px solid #016d80;
    width: 70%;
    font-size: 20px;
    color: white;
    padding: 1%;
    margin-bottom: 20px;
}

#warn{
    font-size: 18px;
    color: #ff2a00;
}
#submit{
    background-color: #06869c;
    border: none;
    color: white;
    font-size: 20px;
    padding: 2%;
    border-radius: 10px;
}
#submit:hover{
    background-color: #026172;
    cursor: pointer;
}
#submit:active{
    transform: translateY(2px);
}
.other-details{
    font-size: 15px;
    color: white;
    background-color: transparent;
    border: none;
    text-decoration: none;
    cursor: pointer;
}
.other-details:hover{
    text-decoration: underline;
}
@media screen and (width<1000px) {
    #login-box{
        padding: 5%;
        width: 80%;
        flex-direction: column-reverse;
        gap: 40px;
    }
    #login-1{
        display: none;
    }
    #login-title{
        font-size: 30px;
        margin-bottom: 10px;
    }
}