#entry-form-box{
    margin-inline: 15%;
    margin-top: 2%;
}
#menu-box{
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #464646;
    border-radius: 20px;
    overflow: hidden;
}
.menu-btn{
    width: 50%;
    padding: 0.5%;
    margin: 0;
    background-color: transparent;
    border: none;
    color: white;
    font-size: 22px;
    cursor: pointer;
}

.menu-btn:hover{
    background-color: #767676;
}
#daily-box{
    display: flex;
    justify-content: space-evenly;
    margin-inline: 15%;
    margin-top: 0.5%;
    margin-bottom: 2%;
}
#month-inp-box{
    margin-top: 20px;
    display: flex;
    justify-content: center;
}
.chart-box{
    margin-top: 50px;
    display: flex;
    justify-content: space-between;
}
.chart{
    width: 400px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    min-height: 100px;
    margin-bottom: 50px;
}
.chart-title{
    font-size: 30px;
    margin-bottom: 20px;
    border-bottom: 5px solid rgb(3, 141, 169);
}
.chart-data{
    width: 100%;
    margin-top: 40px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 10px;
}
.chart-entry{
    font-size: 18px;
    width: 80%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    gap: 20px;
}
.chart-entry-img{
    width: 30px;
}
@media screen and (width<1000px) {
    #daily-box{
        flex-direction: column;
        align-items: center;
        margin-inline: 5%;
        margin-bottom: 15%;
    }
    #expense-box{
        min-width: 90%;
        margin-bottom: 1%;
    }
    #income-box{    
        min-width: 90%;
        margin-bottom: 5%;
    }
    #entry-form-box{
        margin: 5%;
        margin-top: 10%;
    }
    .menu-btn{
        font-size: 20px;
    }
    .chart-box{
        flex-direction: column;
        gap: 40px;
    }
}